body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif, 'Raleway', 'sans-serif';
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  
  .header {
    overflow: hidden;
    background-color: #f1f1f1;
    padding: 20px 10px;
    background-color: #31906a;
    font-family: 'Raleway', sans-serif;
  }
  
  .header a {
    float: left;
    color: white;
    text-align: center;
    padding: 12px;
    text-decoration: none;
    font-size: 18px; 
    line-height: 25px;
    border-radius: 4px;
    
  }
  
  .header a img.logo {
    font-size: 25px;
    font-weight: bold;
    color: #fff;
    width: 35px;
  }
  
  .header a span.title{
    font-size: 40px;
    font-weight: 500;
  }
  
  .header a:hover {
    background-color: #ddd;
    color: black;
  }
  
  .header a.active {
    background-color: #000;
    color: black;
  }
  
  .header-right {
    font-size: 25px;
    font-weight: 500;
    background-color: #31906a;
    color: white;
  }
  
  .header-right a{
    color: black;
    margin: 0 30px 0 30px;
    padding-bottom: 10px;
  }
  .header-right a:hover{
    color: black;
  }
  .header-right a.active{
    color: black;
  }
  
  .NextPage{
    color:#000;
    background: #fff;
    border-radius: 8px;
  }
  
  .NextPage a{
    color:#000;
    background: greenyellow;
  }
  .NextPage a:hover{
    color:#000;
    background: greenyellow;
  
  }
  
  
  
  .PostsFeed{
    display: flex;
    flex-direction: row;
    
    width: 90%;
    margin: 0 auto;
    padding: 20px;
  }
  
  .PostContainer{
    flex-grow: 4; /* default 0 */
    margin: 10px 0 10px 0;
    width: 100px;
    background: #98FB98;
    border: 2px solid white;
    padding: 10px;
  }
  
  .Post{
    padding: 5px; 
    background: 98FB98;
  
  }
  
  @media screen and (max-width: 500px) {
    .header a {
      float: none;
      display: block;
      text-align: left;
    }
    
    .header-right {
      float: none;
    }
  }
  